.certificate-gallery{
	padding: 40px 0 94px;
	.h3{
		font-weight: 600;
		font-size: 36px;
		line-height: 122%;
		text-align: center;
		color: #232323;
		margin-bottom: 70px;
	}
	&__container{
		&::before{
			content: '';
			width: 20%;
			height: 100%;
			z-index: 10;
			background: linear-gradient(to right, #fff 0%, #fff 20%, rgba(255, 255, 255, 0) 100%);
			position: absolute;
			left: 0;
			top: 0;
		}
		&::after{
			content: '';
			width: 20%;
			height: 100%;
			z-index: 10;
			background: linear-gradient(to left, #fff 0%, #fff 20%, rgba(255, 255, 255, 0) 100%);
			position: absolute;
			right: 0;
			top: 0;
		}
		a{
			height: 380px;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			display: flex !important;
			align-items: center;
			justify-content: center;
			img{
				max-width: calc(100% - 20px);
				height: 100%;
			}
		}
		.slick-arrow{
			height: 100%;
			z-index: 100;
			width: 10%;
			background-position: center;
			background-repeat: no-repeat;
			background-size: 30px;
			&::before{
				content: none;
			}
		}
		.slick-prev{
			left: 0%;
			background-image: url(../images/svg/a-l-black.svg);
		}
		.slick-next{
			right: 0%;
			background-image: url(../images/svg/a-r-black.svg);
		}
	}
}
.hr{
	background: #D8DFE2;
	width: 100%;
	height: 1px;
}